import { useCallback, useState } from 'react'

import range from '@utils/numbers/range'
import delay from '@utils/delay'
import Dropdown from '@UI/Dropdown'
import type { DropdownProps } from '@UI/Dropdown/types'

import CLASS_NAME from './styles'

interface Option {
  id: string
  name: string
}

const PAGE_SIZE = 10

const getOptions = (skip: number, take: number, txt = 'Item'): Option[] => {
  return range(skip, skip + take).map(x => {
    const name = `${txt.length === 0 ? 'Item': txt.replace(/#\d*/g, '')} #${x}`
    return { id: name, name }
  })
}

function AsyncDropdown ({ mode }: Partial<DropdownProps<Option>>): JSX.Element {
  const [value1, setValue1] = useState<Option | null>(null)
  const [value2, setValue2] = useState<Option | null>(null)

  const [value3, setValue3] = useState<Option | null>(null)
  const [inputValue3, setInputValue3] = useState<string | null>('Default search')

  const [value4, setValue4] = useState<Option | null>({ id: 'Loaded form value', name: 'Loaded form value' })

  const loadItems = useCallback(async ({ skip, take, search }) => {
    console.info('loadItems :: skip, take, search :: ', skip, take, search)
    const items = await delay(mode === 'select' ? 4000 : 0, getOptions(skip, take, search))
    return { items }
  }, [mode])

  // ========================================== //

  return (
    <div>
      <div className={CLASS_NAME}>
        <Dropdown
          className='cf-async-dropdown'
          createOptionFromInput={(value: string): Option => {
            return { id: value, name: value }
          }}
          fetchHandler={loadItems}
          id='async-dropdown'
          label='Async Dropdown ("create" mode)'
          mode={mode}
          onChange={(_, value) => {
            setValue1(value)
          }}
          pageSize={PAGE_SIZE}
          value={value1}
        />
      </div>

      <div className={CLASS_NAME}>
        <Dropdown
          className='cf-async-dropdown-default'
          fetchHandler={loadItems}
          id='async-dropdown-default'
          label='Async Dropdown (default options)'
          mode={mode}
          onChange={(_, value) => {
            setValue2(value)
          }}
          options={getOptions(0, 5, 'Pre-fetched')}
          pageSize={PAGE_SIZE}
          value={value2}
        />
      </div>

      {(mode === 'search') && (
        <div className={CLASS_NAME}>
          <p>If <span>inputValue</span> is set (controlled), it will work as a filter for fetcher.</p>

          <Dropdown
            className='cf-async-dropdown-controlled-input'
            fetchHandler={loadItems}
            id='async-dropdown-controlled-input'
            inputValue={inputValue3}
            label='Async Dropdown (controlled inputValue)'
            mode={mode}
            onChange={(id, value) => {
              console.info(id, 'onChange :: ', value)
              setValue3(value)
              setInputValue3(value?.name ?? '')
            }}
            onChangeInput={(value) => {
              console.info('async-dropdown-controlled-input', 'onChangeInput :: ', value)
              setInputValue3(value)
            }}
            pageSize={PAGE_SIZE}
            value={value3}
          />
        </div>
      )}

      {(mode === 'search') && (
        <div className={CLASS_NAME}>
          <p>If <span>value</span> is set (controlled), it will not filter options.</p>

          <Dropdown
            className='cf-async-dropdown-controlled-with-initial-value'
            fetchHandler={loadItems}
            id='async-dropdown-controlled-with-initial-value'
            label='Async Dropdown (controlled value)'
            mode={mode}
            onChange={(id, value, reason) => {
              console.info(id, 'onChange :: ', value, reason)
              setValue4(value)
            }}
            pageSize={PAGE_SIZE}
            value={value4}
          />
        </div>
      )}
    </div>
  )
}

function DropdownExampleHOC (hocProps: Partial<DropdownProps<Option>>) {
  return (props: Omit<DropdownProps<Option>, 'id'>) => (<AsyncDropdown {...hocProps} {...props} />)
}

export const AsyncSearchExample = DropdownExampleHOC({ mode: 'search' })
export const AsyncSelectExample = DropdownExampleHOC({ mode: 'select' })

