export const SelectExpandColapse = () => (
  <div style={{ display: 'flex', flexDirection: 'column', gap: '20px' }}>
    <SelectWithLabelExample style={{ maxWidth: 400 }} />

    <div style={{ marginTop: 250 }}>
      <Mermaid chart={`
        stateDiagram-v2
          [*] --> Idle

          Idle --> FocusedExpanded: click on SelectDisplay
          Idle --> FocusedExpanded: click on Trigger
          FocusedExpanded --> FocusedCollapsed: click on Trigger
          FocusedExpanded --> FocusedCollapsed: click on SelectDisplay
          FocusedExpanded --> FocusedCollapsed: click on Option/Action
          FocusedExpanded --> Idle: click outside

          Idle --> FocusedCollapsed: click on Clear
          FocusedCollapsed --> FocusedExpanded: click on Trigger
          FocusedCollapsed --> Idle: click outside
      `} />
    </div>
  </div>
)

export function FormSelectExample () {
  const [options] = useState(DEFAULT_STRING_OPTIONS())
  const [value, setValue] = useState<string | null>(null)
  const [error, setError] = useState<string | null>(null)

  return (
    <FormDropdown
      id='form-select-example'
      error={error}
      style={{ minWidth: 400 }}
      mode='select'
      value={value}
      options={options}
      touched
      onChange={(_, value) => {
        setValue(value)

        if (value === options[2]) {
          setError('Ups, this is not allowed')
        } else {
          setError(null)
        }
      }}
    />
  )
}
