import { useEffect, useState, useRef } from 'react'

import { range } from '@utils/numbers'
import Dropdown from '@UI/Dropdown'
import type { DropdownBlurMode, DropdownProps } from '@UI/Dropdown/types'
import NumberInput from '@UI/NumberInput'
import Input from '@UI/Input'

import CLASS_NAME from './styles'

interface Option {
  id: number
  name: string
}

const BLUR_MODES_OPTIONS = [
  { id: 'clear', name: '"clear" mode' },
  { id: 'create', name: '"create" mode' },
  { id: 'select-first', name: '"select-first" mode' }
]

const getOptions = (num: number): Option[] => {
  return range(num).map(x => ({ id: x, name: `Item #${x}` }))
}

function ControlledDropdown (props: Partial<DropdownProps<Option>>): JSX.Element {
  const [optionsNum, setOptionsNum] = useState(100)
  const [selectedOptionIdx, setSelectedOptionIdx] = useState<null | number>(null)
  const [isDropdownOpenControlled, setIsDropdownOpenControlled] = useState(false)
  const [isError, setIsError] = useState(false)
  const [isDropdownOpen, setIsDropdownOpen] = useState(false)
  const [options, setOptions] = useState<Option[]>(getOptions(100))
  const [value, setValue] = useState<Option | null>(null)
  const [blurMode, setBlurMode] = useState<DropdownBlurMode>('clear')
  const [inputValue, setInputValue] = useState<string | null>('')

  const refOptions = useRef(options)

  // ========================================== //
  //                   EFFECTS                  //
  // ========================================== //

  useEffect(() => {
    const runEffect = () => {
      refOptions.current = getOptions(optionsNum)
      setOptions(refOptions.current)
      setSelectedOptionIdx(prev => ((prev != null) && (optionsNum > 0)) ? Math.min(prev, optionsNum - 1) : null)
    }

    runEffect()
    return () => {}
  }, [optionsNum])

  useEffect(() => {
    const runEffect = () => {
      const options = refOptions.current

      if (selectedOptionIdx == null) {
        setValue(null)
        setInputValue(null)
        return
      }

      if (selectedOptionIdx < options.length) {
        const option = options[selectedOptionIdx]
        setValue(option)
        setInputValue(option.name)
      }
    }

    runEffect()
    return () => {}
  }, [selectedOptionIdx])

  // ========================================== //

  return (
    <div className={CLASS_NAME}>
      <div className='cf-actions-row'>
        <div className='cf-actions-cell'>
          <NumberInput
            autoComplete='off'
            blurComplementMode='min-scale'
            className='cf-options-number'
            isEmptyAllowed={false}
            id='options-number'
            label='Options number'
            min={0}
            onChange={(_, value) => {
              setOptionsNum(value)
            }}
            value={optionsNum}
          />

          <div className='cf-actions-row'>
            <div className='cf-actions-cell'>
              <label>
                <input
                  type='checkbox'
                  checked={isDropdownOpenControlled}
                  onChange={(evt) => {
                    setIsDropdownOpenControlled(evt.target.checked)
                  }}
                />
                <span style={{ marginLeft: '4px' }}>Control isOpen?</span>
              </label>

              <label>
                <input
                  type='checkbox'
                  checked={isError}
                  onChange={(evt) => {
                    setIsError(evt.target.checked)
                  }}
                />
                <span style={{ marginLeft: '4px' }}>Is error?</span>
              </label>
            </div>

            <label>
              <input
                type='checkbox'
                checked={isDropdownOpen}
                onChange={(evt) => {
                  setIsDropdownOpen(evt.target.checked)
                }}
              />
              <span style={{ marginLeft: '4px' }}>Is opened</span>
            </label>
          </div>
        </div>

        <div className='cf-actions-cell'>
          <NumberInput
            autoComplete='off'
            className='cf-selected-option-id'
            id='selected-option-idx'
            label='Selected option (index)'
            max={optionsNum -1}
            min={0}
            onChange={(_, value) => {
              setSelectedOptionIdx(value)
            }}
            value={selectedOptionIdx}
          />

          {(props.mode === 'search') && (
            <Dropdown
              className='cf-dropdown-blur-modes-select'
              defaultValue={BLUR_MODES_OPTIONS[0]}
              id='dropdown-blur-modes-select'
              label='Blur mode'
              mode='select'
              onChange={(id) => {
                setBlurMode(id as DropdownBlurMode)
              }}
              options={BLUR_MODES_OPTIONS}
            />
          )}
        </div>

        <div className='cf-actions-cell'>
          {(props.mode === 'search') && (
            <Input
              id='controlled-input-value'
              onChange={(_, value) => {
                setInputValue(value as string)
              }}
              label='Dropdown search content'
              value={inputValue}
            />
          )}
        </div>
      </div>

      <Dropdown
        blurMode={blurMode}
        className='cf-controlled-dropdown'
        createOptionFromInput={(value: string): Option => {
          return { id: Date.now(), name: value }
        }}
        defaultValue={options[0]}
        error={isError}
        id='controlled-dropdown'
        isOpen={isDropdownOpenControlled ? isDropdownOpen : undefined}
        inputValue={inputValue}
        label='Controlled Dropdown Example'
        mode={props.mode}
        onChangeInput={(value: string | null, reason) => {
          console.info('controlled-dropdown :: onChangeInput :: ', value, reason)
          setInputValue(value)
        }}
        onChange={(_, value, reason) => {
          console.info('controlled-dropdown :: onChange :: ', value, reason)

          if ((reason === 'blur-create') && (value != null)) {
            refOptions.current = options.concat([value])
            setOptions(refOptions.current)
          }

          setSelectedOptionIdx(value != null ? refOptions.current.indexOf(value) ?? null : null)
        }}
        options={options}
        value={value}
      />
    </div>
  )
}

function DropdownExampleHOC (hocProps: Partial<DropdownProps<Option>>) {
  return (props: Omit<DropdownProps<Option>, 'id'>) => (<ControlledDropdown {...hocProps} {...props} />)
}

export const ControlledSearchExample = DropdownExampleHOC({ mode: 'search' })
export const ControlledSelectExample = DropdownExampleHOC({ mode: 'select' })
