import { cnx } from '@utils/ui'
import isStringNotEmpty from '@utils/isStringNotEmpty'
import isError from '@utils/isError'

import { type SelectDisplayProps } from '../../types'
import CLASS_NAME from './styles'

function SelectDisplay <TOption> ({
  endAdornment,
  error,
  disabled,
  label,
  onBlur,
  onClick,
  onFocus,
  containerRef,
  startAdornment,
  value
}: SelectDisplayProps<TOption>): JSX.Element {
  const className = cnx(
    CLASS_NAME,
    'cf-select-display-container',
    {
      'with-adornment': startAdornment != null,
      'with-label': isStringNotEmpty(label),
      'with-value': Boolean(value),
      'with-error': isError(error),
      disabled
    }
  )

  // ========================================== //

  return (
    <div
      className={className}
      onBlur={onBlur}
      onClick={onClick}
      onFocus={onFocus}
      ref={containerRef}
      tabIndex={0}
    >
      {(startAdornment != null) && (
        <div className={cnx('cf-start-adornment', 'cf-display-adornment')}>
          {startAdornment}
        </div>
      )}

      <div className='cf-display'>
        {value}
      </div>

      {(label != null) && (
        <div className='cf-display-label'>
          {label}
        </div>
      )}

      {(endAdornment != null) && (
        <div className={cnx('cf-end-adornment', 'cf-display-adornment')}>
          {endAdornment}
        </div>
      )}
    </div>
  )
}

export default SelectDisplay
