import getHash from '@utils/getHash'
import type { Items } from './types'

export const defaultFormatDisplayValue = (item: any): string => {
  return (item?.name ?? item?.label ?? item?.title ?? item?.id ?? item ?? '')?.toString()
}

export const defaultFilterOptions = <TOption> (options: TOption[], inputValue: string | null): TOption[] => {
  return options.filter(x => defaultFormatDisplayValue(x).includes(inputValue ?? ''))
}

export const mapItemsToOptions = <TOption> (items: Items<TOption>): TOption[] => {
  return items.map(x => x.value)
}

export const mapOptionsToItems = <TOption> (options?: TOption[] | null): Items<TOption> => {
  return (options ?? []).map(x => ({ id: (x as any)?.id ?? getHash(x), value: x }))
}

export const getOptionId = <TOption> (option: TOption): string => {
  return (option as any)?.id?.toString() ?? getHash(option)
}
