import { Dispatch, SetStateAction, useCallback, useEffect, useRef, useState } from 'react'
import pDebounce from 'p-debounce'
import useRefUpdater from '@hooks/useRefUpdater'

export interface UseAutocompleteInfiniteListReturn<T> {
  isLoading: boolean
  items: T[]
  getItems: (search?: string, isRefreshing?: boolean) => Promise<void>
  resetState: () => void
  setSearch: Dispatch<SetStateAction<string>>
  search: string
}

export interface FetchOptions {
  skip: number
  take: number
  search: string
}

export interface UseAutocompleteInfiniteListProps<T> {
  isAutorun?: boolean
  fetch: FetchHandler<T>
  pageSize?: number
  propIdName: string
}

export type FetchHandler<T> = (payload: FetchOptions) => Promise<{ items: T[] }>

const PAGE_SIZE = 20
const AUTO_SCROLL = 0.2
const NO_AUTO_SCROLL_ITEMS_OFFSET = 8
const FETCH_DELAY = 1000
const DEFAULT_ITEMS_COUNT = { all: 0, prev: -1 }

const getInitLoadOptions = (pageSize: number): FetchOptions => ({
  search: '',
  skip: 0,
  take: pageSize
})

/**
 * Used for autocomplete components which needs to fetch items on scroll of its listBox.
 * It isolates:
 * - loading flag management;
 * - getItems callback which is used for: initial load; refreshing load when search changes; loadMore on scroll.
 * - scroll lower when items are loaded for better UX
 */

const useAutocompleteInfiniteList = <T>({
  isAutorun = true,
  fetch,
  pageSize = PAGE_SIZE,
  propIdName
}: UseAutocompleteInfiniteListProps<T>): UseAutocompleteInfiniteListReturn<T> => {
  const [isLoading, setIsLoading] = useState(false)
  const [search, setSearch] = useState('')
  const [items, setItems] = useState<T[]>([])
  const itemsCountRef = useRef({ ...DEFAULT_ITEMS_COUNT })
  const fetchOptionsRef = useRef({ search, options: getInitLoadOptions(pageSize) })
  const fetchRef = useRef(fetch)
  const refIsAutorun = useRefUpdater(isAutorun)
  const refIsFirstRun = useRef(true)
  const refPageSize = useRefUpdater(pageSize)

  // ========================================== //
  //                   HANDLERS                 //
  // ========================================== //

  const _getItems = useCallback(pDebounce(async (search?: string, isRefreshing: boolean = false): Promise<void> => {
    try {
      if (isRefreshing) {
        fetchOptionsRef.current.options = getInitLoadOptions(refPageSize.current)
      } else if (itemsCountRef.current.prev === 0) {
        /**
         * DD-NOTE:
         * we do not want infinite loading attempts in the end of the scroll section.
         * So we return if last request returned zero results and we do not refresh.
         * Ideally we need to have 'hasMore' prop - (discuss with the backend)
         */
        return
      }

      const res = await fetchRef.current({
        ...fetchOptionsRef.current.options,
        search: search ?? fetchOptionsRef.current.search
      })

      itemsCountRef.current.prev = res.items.length
      setItems(prev => isRefreshing ? res.items : prev.concat(res.items))

      fetchOptionsRef.current.options.skip += refPageSize.current
    } catch {
      console.error('fetching items failed')
    } finally {
      setIsLoading(false)
    }
  }, FETCH_DELAY), [])

  /**
   * we need to start spinner as soon as the request starts
   */
  const getItems = useCallback(async (search?: string, isRefreshing: boolean = false): Promise<void> => {
    setIsLoading(true)
    void _getItems(search, isRefreshing)
  }, [_getItems])

  const resetState = useCallback(() => {
    refIsFirstRun.current = true
    itemsCountRef.current = { ...DEFAULT_ITEMS_COUNT }
    fetchOptionsRef.current = { search: '', options: getInitLoadOptions(pageSize) }
    setIsLoading(false)
    setItems([])
  }, [])

  // ========================================== //
  //                   EFFECTS                  //
  // ========================================== //

  /**
   * This allows not to memoize fetch in arguments
   */
  useEffect(() => {
    fetchRef.current = fetch
  }, [fetch])

  /**
   * Not always it is possible/convenient to pass 'search' arg to getItems as it can be used outside the hook
   * that is why we also store latest 'search' value in a ref
   */
  useEffect(() => {
    fetchOptionsRef.current.search = search
  }, [search])

  /**
   * used to scroll a little bit lower when new items are loaded (better UX)
   */
  useEffect(() => {
    const prevAll = itemsCountRef.current.all
    itemsCountRef.current.all = items.length

    /**
     * We should not auto-scroll when we are refreshing
     * or if number of items loaded is less than a default small threshold
     * items.length < prevAll -> means we are refreshing
     */
    if (items.length < prevAll || prevAll <= NO_AUTO_SCROLL_ITEMS_OFFSET) {
      return
    }

    const listBox = document.getElementById(`${propIdName}-popup`)
    listBox?.style.setProperty('scroll-behavior', 'smooth')

    if (listBox != null) {
      listBox.scrollTop += listBox.clientHeight * AUTO_SCROLL
      listBox?.style.setProperty('scroll-behavior', 'none')
    }
  }, [items.length, propIdName])

  /**
   * getItems with 'isRefreshing' = true when search changes
   */
  useEffect(() => {
    if (!refIsAutorun.current && refIsFirstRun.current) {
      refIsFirstRun.current = false
      return
    }

    void getItems(search, true)
  }, [getItems, search])

  // ========================================== //

  return {
    isLoading,
    items,
    getItems,
    resetState,
    setSearch,
    search
  }
}

export default useAutocompleteInfiniteList
