import { cnx } from '@utils/ui'

import type { ListboxProps } from '../../types'
import { CLASS_ACTION_ITEM, CLASS_LISTBOX_ITEM } from '../../constants'
import DefaultOption from '../Option'
import DefaultNoOptions from '../NoOptions'
import Loader from '../Loader'
import CLASS_NAME from './styles'

const CLASS_OPTION_ITEM = 'cf-option-item'

function Listbox <TOption> ({
  actions = [],
  id,
  isLoading = false,
  isOptionSelected,
  onClickItem,
  onScroll,
  options = [],
  renderAction = DefaultOption,
  renderNoOptions = 'No items',
  renderOption = DefaultOption
}: ListboxProps<TOption>): JSX.Element {
  const isEmpty = (actions.length === 0) && (options.length === 0)

  return (
    <div
      className={cnx(CLASS_NAME, 'cf-dropdown-listbox')}
      id={id}
      onScroll={onScroll}
    >
      {isEmpty && !isLoading && (typeof renderNoOptions === 'string') && (
        <DefaultNoOptions>
          {renderNoOptions}
        </DefaultNoOptions>
      )}

      {isEmpty && !isLoading && (typeof renderNoOptions !== 'string') && renderNoOptions({})}

      {actions.map(item => (
        <div
          className={cnx(CLASS_LISTBOX_ITEM, CLASS_ACTION_ITEM)}
          id={item.id}
          key={item.id}
          onClick={onClickItem}
        >
          {item.renderFn?.(item) ?? renderAction(item)}
        </div>
      ))}

      {options.map(item => (
        <div
          className={cnx(CLASS_LISTBOX_ITEM, CLASS_OPTION_ITEM, isOptionSelected(item) && 'cf-option-selected')}
          id={item.id}
          key={item.id}
          onClick={onClickItem}
        >
          {renderOption(item.value)}
        </div>
      ))}

      {isLoading && <Loader />}
    </div>
  )
}

export default Listbox
