import { type FC } from 'react'

import { cnx } from '@utils/ui'
import CrossIcon from '@UI/icons/CrossIcon'
import DropdownOpenIcon from '@UI/icons/DropdownOpenIcon'
import DropdownCloseIcon from '@UI/icons/DropdownCloseIcon'

import type { TriggerProps } from '../../types'
import CLASS_NAME from './styles'

const Trigger: FC<TriggerProps> = ({
  isClearVisible,
  isOpen,
  onClickTrigger,
  onMouseDownClear
}) => {
  return (
    <div className={cnx(CLASS_NAME, 'cf-dropdown-trigger-root')}>
      <div className='cf-dropdown-clear-button' onMouseDown={onMouseDownClear}>
        {isClearVisible && <CrossIcon />}
      </div>

      <div className='cf-dropdown-trigger-button' onClick={onClickTrigger}>
        {isOpen ? <DropdownCloseIcon /> : <DropdownOpenIcon />}
      </div>
    </div>
  )
}

export default Trigger
