import EventEmitter from 'events'
import {
  useCallback,
  useEffect,
  useState,
  useRef,
  useMemo,
  type ChangeEvent,
  type FocusEvent,
  type MouseEvent,
  type SyntheticEvent
} from 'react'

import useRefUpdater from '@hooks/useRefUpdater'
import useAutocompleteInfiniteList, { type FetchHandler } from '@hooks/useAutocompleteInfiniteList'
import isStringNotEmpty from '@utils/isStringNotEmpty'

import { CLASS_ACTION_ITEM, CLASS_LISTBOX_ITEM } from './constants'
import type {
  Action,
  ChangeEventHandler,
  Item,
  Items,
  ConditionalValue,
  UseDropdownProps,
  UseDropdownReturn,
  DropdownValueChangeReason
} from './types'

import {
  defaultFilterOptions,
  defaultFormatDisplayValue,
  mapOptionsToItems,
  mapItemsToOptions,
  getOptionId
} from './utils'

type DropdownUIState = 'idle' | 'focused-expanded' | 'focused-collapsed'

type DropdownUIAction =
  | 'clear-click'
  | 'container-blur'
  | 'input-change'
  | 'input-click'
  | 'option-click'
  | 'popover-close-requested'
  | 'select-display-click'
  | 'trigger-click'

const ACTION_EVENT = 'action-triggered'
const DELAY_FOR_CLOSING_POPOVER = 200
const DEFAULT_ACTIONS: Action[] = []
const DEFAULT_BOTTOM_THRESHOLD = 10 // 100
const WARN_CREATE_MODE = 'For the "create" blurMode the "createOptionFromInput" option is required. Switched to "clear" mode.'
const SCROLL_TOP_TRIGGER_OFFSET = 10 // 100

/**
 * Dropdown's behaviour hook.
 * Implements all logic needed to handle interactions with a typical Dropdown component:
 *   - expand/collapse
 *   - input change
 *   - option(s) selection
 *   - keyboard navigation
 *
 * UX decisions:
 *   - when 'isMultiselect=true' - expanded listbox is not closed by click on items.
 *   - when 'isMultiselect=false' - click on item closes listbox and leaves Dropdown focused.
 *   - when `fetchHandler` is set - requests to BE will be sent only on Dropdown expand (no prefetch).
 *   - when 'focus' - expand Dropdown.
 *   - when expand a dropdown with selected item - always show a full list of options.
 *   - when type in an input of a dropdown - always show filtered items.
 *
 * DevEx decisions:
 *   - we will filter visible options after each change of `props.inputValue`.
 *      - this means that even controlled `props.options` will be filtered (a.k.a. semi-controlled).
 *      - without this, we will need to implement filter options in `onChangeInput` each time.
 *   - if `props.fetchActions` is set - `props.options` will be used only as initial (default) props.
 *   - `props.value` is displayed even if it does not exist in `props.options`.
 */
function useDropdown <TOption, TIsMultiple extends boolean = false> ({
  actions = DEFAULT_ACTIONS,
  blurMode: _blurMode,
  createOptionFromInput,
  defaultValue,
  disabled = false,
  disableClearable = false,
  id: fieldId,
  isDebug = false,
  isOpen: _isDropdownOpen,
  isMultiselect: _isMultiselect,
  inputValue: _inputValue,
  fetchHandler: _fetchHandler,
  filterOptions = defaultFilterOptions,
  formatDisplayValue = defaultFormatDisplayValue,
  mode = 'search',
  onChange: _onChange,
  onChangeInput: _onChangeInput,
  options,
  pageSize,
  refInput,
  refSelectDisplay,
  value
}: UseDropdownProps<TOption, TIsMultiple>): UseDropdownReturn<TOption> {
  /**
   * Essential state (refs also).
   */
  const [isDropdownOpen, _setIsDropdownOpen] = useState(_isDropdownOpen ?? false)
  const [rawOptions, setRawOptions] = useState<TOption[]>(options ?? [])
  const [itemsToRender, _setItemsToRender] = useState<Items<TOption>>(mapOptionsToItems(options))
  const [selectedItems, setSelectedItems] = useState<Items<TOption>>([])
  const isAsync = _fetchHandler != null
  const isMultiselect = Boolean(_isMultiselect ?? false) // TS
  const refSelectedItems = useRef<Map<string, Item<TOption>>>(new Map([]))
  const refIsDefaultValueUsed = useRef(false)
  const refPrevScrollTopTrigger = useRef(0)

  /**
   * These two values are closely related, but nt equal.
   * Change of the `searchValue` always leads to change of `displayValue`. So user will see in input,
   * what they typed.
   * Contrary, cahnge of the `displayValue` (for example, by selected option) - can lead only to cleaning
   * of the `searchValue`.
   */
  const [displayValue, setDisplayValue] = useState<string | null>(_inputValue ?? null)
  const [searchValue, setSearchValue] = useState<string | null>(_inputValue ?? null)
  const isClearVisible = Boolean(displayValue) || (selectedItems.length > 0)

  /**
   * State machine internals.
   */
  const refEventBus = useRef(new EventEmitter())
  const refLastUIAction = useRef<DropdownUIAction | null>(null)
  const refPrevUIAction = useRef<DropdownUIAction | null>(null)
  const refUIState = useRef<DropdownUIState>('idle')
  const refPrevUIState = useRef<DropdownUIState>('idle')
  const refIsDisplayFocused = useRef(false)

  /**
   * Deps, race conditions.
   */
  const refItemsToRender = useRef(itemsToRender)
  const refIsMultiselect = useRefUpdater(isMultiselect)
  const refInputValue = useRefUpdater(_inputValue)
  const refRawOptions = useRefUpdater(rawOptions)
  const refMode = useRefUpdater(mode)
  const refIsDropdownOpen = useRef(isDropdownOpen)

  const debug = useCallback((...args: any[]) => {
    console.debug(`${fieldId} :: `, ...args)
  }, [fieldId, isDebug])

  const blurMode = useMemo(() => {
    if ((createOptionFromInput === undefined) && (_blurMode === 'create')) {
      console.error(WARN_CREATE_MODE)
      return 'clear'
    }

    return _blurMode ?? (disableClearable ? 'select-first' : 'clear')
  }, [
    _blurMode,
    createOptionFromInput,
    disableClearable
  ])

  // ========================================== //
  //                   HELPERS                  //
  // ========================================== //

  const setIsDropdownOpen = useCallback((isOpen: boolean) => {
    refIsDropdownOpen.current = isOpen
    _setIsDropdownOpen(isOpen)
  }, [])

  const fetchHandler: FetchHandler<TOption> = useCallback(async (...args) => {
    if ((_fetchHandler == null) || !refIsDropdownOpen.current) {
      return { items: [] }
    }

    return await _fetchHandler(...args)
  }, [_fetchHandler])

  const {
    isLoading,
    getItems,
    items: fetchedOptions,
    resetState: resetFetcherState,
    setSearch
  } = useAutocompleteInfiniteList({
    isAutorun: false,
    fetch: fetchHandler,
    pageSize,
    propIdName: fieldId
  })

  const _focusInput = useCallback(() => {
    if (!refIsDisplayFocused.current) {
      refInput.current?.focus()
    }
  }, [])

  const _focusSelectDisplay = useCallback(() => {
    if (!refIsDisplayFocused.current) {
      refSelectDisplay.current?.focus()
    }
  }, [])

  const focusDisplay = useCallback(() => {
    if (disabled) {
      return
    }

    if (mode === 'select') {
      _focusSelectDisplay()
    } else if (mode === 'search') {
      _focusInput()
    }
  }, [
    disabled,
    mode,
    _focusSelectDisplay,
    _focusInput
  ])

  const openDropdown = useCallback(() => {
    if (disabled || isDropdownOpen) {
      return
    }

    setIsDropdownOpen(true)

    if (isAsync) {
      debug('openDropdown :: fired getItems by search :: ', searchValue)
      setRawOptions([])
      void getItems(searchValue ?? '', true)
    }
  }, [
    disabled,
    searchValue,
    fetchedOptions,
    isAsync,
    isDropdownOpen,
    setIsDropdownOpen
  ])

  const closeDropdown = useCallback(() => {
    if (disabled || !isDropdownOpen) {
      return
    }

    setIsDropdownOpen(false)
  }, [
    disabled,
    isDropdownOpen,
    setIsDropdownOpen
  ])

  const clearSelected = useCallback(() => {
    if (disabled) {
      return
    }

    refSelectedItems.current = new Map()
    setSelectedItems([])
  }, [disabled])

  const gotoState = useCallback((nextUIState: DropdownUIState) => {
    debug(`sm :: "${refUIState.current}" ======> "${nextUIState}"`)
    refPrevUIState.current = refUIState.current
    refUIState.current = nextUIState
  }, [])

  const dispatchUIAction = useCallback((uiAction: DropdownUIAction) => {
    debug(`sm :: dispatched action "${uiAction}"`)
    refLastUIAction.current = uiAction
    refEventBus.current.emit(ACTION_EVENT)
  }, [])

  const setItemsToRender = useCallback((items: Items<TOption>): void => {
    refItemsToRender.current = items
    _setItemsToRender(items)
  }, [])

  const setItemsToRenderByFilter = useCallback((inputValue: string | null) => {
    const rawOptions = refRawOptions.current
    const selectedItems = Array.from(refSelectedItems.current.values())
    const filteredOptions = filterOptions(rawOptions, inputValue ?? '', mapItemsToOptions(selectedItems))
    const filteredItems = mapOptionsToItems(filteredOptions)

    setItemsToRender(filteredItems)
  }, [filterOptions, setItemsToRender])

  /**
   * Not a real handler, just a TS reconciler.
   */
  const onChange = useMemo(() => {
    /**
     * Check this for more details:
     * https://www.javiercasas.com/articles/typescript-dependent-types
     * Although even this did not help for this specific case (but worked for more simple cases).
     * AZ-TODO: try again, for honor.
     */
    function getOnChangeFn <T, M extends boolean> (isMultiselect: M): ChangeEventHandler<ConditionalValue<T[], T | null, M>> | undefined
    function getOnChangeFn <T> (isMultiselect: boolean): ChangeEventHandler<ConditionalValue<T[], T | null, typeof isMultiselect>> | undefined {
      return _onChange as any
    }

    return getOnChangeFn<TOption, typeof isMultiselect>(isMultiselect)
  }, [_onChange, isMultiselect])

  /**
   * Processes all logic related to a "proper" option (not action) selection,
   * including call of related handler and calculation of display value.
   */
  const selectOption = useCallback((id: string, reason: DropdownValueChangeReason = 'select-option') => {
    const items = reason === 'blur-select-first'
      ? mapOptionsToItems(refRawOptions.current) // AZ-TODO: optimize
      : itemsToRender

    const item = items.find(x => getOptionId(x.value) === id)

    if (item == null) {
      return
    }

    if (refIsMultiselect.current) {
      return // TBD
    }

    refSelectedItems.current = new Map([[item.id, item]])

    if (onChange != null) {
      onChange(fieldId, item.value, reason)
    } else {
      setSelectedItems([item])
    }

    setSearchValue(null)
    setDisplayValue(formatDisplayValue(item.value))
  }, [
    formatDisplayValue,
    fieldId,
    itemsToRender,
    onChange,
    rawOptions
  ])

  const delayToNotBlickWhilePopoverIsClosing = useCallback((fn: Function) => {
    setTimeout(fn, DELAY_FOR_CLOSING_POPOVER)
  }, [])

  /**
   * Respect this handler, or else...
   */
  const processDropdownBlur = useCallback(() => {
    if (mode === 'select') {
      return
    }

    if (isMultiselect) {
      return // TBD
    }

    /**
     * Synced input means that it shows same text that was visible after direct
     * selection of some option.
     */
    const isSyncedInput = formatDisplayValue(selectedItems[0]?.value) === displayValue

    if (blurMode === 'clear') {
      if (isSyncedInput) {
        /**
         * Use case:
         *   1. User selects option "Foo" in by click on the option.
         *   2. User focus in input, removes last "o" then types "o".
         *   3. User collapses Dropdown (by outside click or smth else).
         *   4. User opens Dropdown and see only items *filtered* by "Foo".
         *
         *   Expected result: User sees all options (like it was after they clicked on "Foo" option).
         */
        if (rawOptions.length !== itemsToRender.length) {
          delayToNotBlickWhilePopoverIsClosing(() => {
            setItemsToRenderByFilter(null)
          })
        }
        return
      }

      const inputValue = null
      delayToNotBlickWhilePopoverIsClosing(() => {
        if (onChange != null) {
          onChange(fieldId, null, 'blur-clear')
        } else {
          clearSelected()
          setItemsToRenderByFilter(inputValue)
        }

        if (_onChangeInput !== undefined) {
          _onChangeInput?.(inputValue, 'blur-clear')
        } else {
          setDisplayValue(inputValue)
          setSearchValue(inputValue)
        }
      })

      return
    }

    if ((blurMode === 'create') && !isSyncedInput && isStringNotEmpty(displayValue)) {
      if ((createOptionFromInput == null) || (displayValue == null)) {
        console.error(WARN_CREATE_MODE)
        return
      }

      delayToNotBlickWhilePopoverIsClosing(() => {
        const option = createOptionFromInput(displayValue)
        refRawOptions.current = refRawOptions.current.concat([option])
        setRawOptions(refRawOptions.current)

        const item = mapOptionsToItems([option])[0] // we need only first item
        if (item != null) {
          selectOption(getOptionId(item), 'blur-create')
          _onChangeInput?.(formatDisplayValue(item.value), 'blur-create')
          setItemsToRenderByFilter(null)
        }
      })

      return
    }

    if ((blurMode === 'select-first') && !isSyncedInput) {
      const item = mapOptionsToItems(rawOptions.slice(0, 1))[0] // we need only first item
      if (item != null) {
        delayToNotBlickWhilePopoverIsClosing(() => {
          selectOption(getOptionId(item), 'blur-select-first')
          _onChangeInput?.(formatDisplayValue(item.value), 'blur-select-first')
          setItemsToRenderByFilter(null)
        })
      }
    }
  }, [
    blurMode,
    clearSelected,
    createOptionFromInput,
    delayToNotBlickWhilePopoverIsClosing,
    displayValue,
    fieldId,
    isMultiselect,
    itemsToRender,
    mode,
    onChange,
    _onChangeInput,
    selectedItems,
    selectOption,
    setItemsToRenderByFilter,
    rawOptions
  ])

  const isOptionSelected = useCallback((item: Item<TOption>): boolean => {
    return refSelectedItems.current.has(item.id)
  }, [])

  // ========================================== //
  //                   HANDLERS                 //
  // ========================================== //

  const onMouseDownClear = useCallback((evt: MouseEvent<HTMLDivElement>) => {
    evt.preventDefault()

    if (disabled) {
      return
    }

    const inputValue = null
    setSearchValue(inputValue)
    setDisplayValue(inputValue)

    if (onChange != null) {
      onChange(fieldId, null, 'clear')
    } else {
      clearSelected()
      setItemsToRenderByFilter(inputValue)
    }

    _onChangeInput?.(inputValue, 'input')
    dispatchUIAction('clear-click')
  }, [
    clearSelected,
    disabled,
    fieldId,
    onChange,
    setItemsToRenderByFilter
  ])

  const onClickTrigger = useCallback((_: MouseEvent<HTMLDivElement>) => {
    if (!disabled) {
      dispatchUIAction('trigger-click')
    }
  }, [disabled, dispatchUIAction])

  const onFocusContainer = useCallback((_: FocusEvent<HTMLDivElement>) => {
    debug('onFocusContainer :: start')
    // TBD
  }, [])

  const onBlurContainer = useCallback((_: FocusEvent<HTMLDivElement>) => {
    debug('onBlurContainer :: start')
    dispatchUIAction('container-blur')
    // TBD
  }, [])

  // ------------------------------ //
  // Search (input)                 //
  // ------------------------------ //

  /**
   * Updates search content.
   * Also, in the case of uncontrolled Dropdown - will filter visible options.
   *
   * In the case of controlled Dropdown - the same operation will be done
   * in the related effect (EffectInputValue), assuming the chain:
   *   user input --> props.onChangeInput --> props.inputValue changed --> effect
   */
  const onChangeInput = useCallback((evt: ChangeEvent<HTMLInputElement>) => {
    const inputValue = evt.target.value

    if (_onChangeInput !== undefined) {
      _onChangeInput(inputValue, 'input', evt)
    } else {
      setSearchValue(inputValue)
      setDisplayValue(inputValue)
    }

    if (isAsync) {
      debug('hook :: onChangeInput :: ', inputValue)
      void getItems(inputValue, true)
      setRawOptions([])
    } else {
      setItemsToRenderByFilter(inputValue)
    }

    dispatchUIAction('input-change')
  }, [
    _onChangeInput,
    getItems,
    isAsync,
    resetFetcherState,
    setItemsToRender,
    setItemsToRenderByFilter,
    setSearch
  ])

  /**
   * Implements requirement:
   *   - when 'focus' - expand Dropdown.
   */
  const onFocusInput = useCallback((_: FocusEvent<HTMLInputElement>) => {
    refIsDisplayFocused.current = true
  }, [])

  const onBlurInput = useCallback((_: FocusEvent<HTMLInputElement>) => {
    refIsDisplayFocused.current = false
  }, [])

  const onClickInput = useCallback((_: MouseEvent<HTMLInputElement>) => {
    dispatchUIAction('input-click')
  }, [dispatchUIAction])

  // ------------------------------ //
  // Select (div)                   //
  // ------------------------------ //

  /**
   * Implements requirement:
   *   - when 'focus' - expand Dropdown.
   */
  const onFocusSelectDisplay = useCallback((_: FocusEvent<HTMLInputElement>) => {
    refIsDisplayFocused.current = true
  }, [])

  const onBlurSelectDisplay = useCallback((_: FocusEvent<HTMLInputElement>) => {
    refIsDisplayFocused.current = false
  }, [])

  /**
   * Allows to expand Dropdown even if it's already focused.
   * This is a convenient UX for the 'select' mode.
   */
  const onClickSelectDisplay = useCallback((_: MouseEvent<HTMLInputElement>) => {
    if (!disabled) {
      dispatchUIAction('select-display-click')
    }
  }, [disabled, dispatchUIAction])

  // ------------------------------ //
  // Listbox                        //
  // ------------------------------ //

  const onClosePopover = useCallback(() => {
    dispatchUIAction('popover-close-requested')
  }, [dispatchUIAction])

  const onClickListboxItem = useCallback((evt: MouseEvent<HTMLDivElement>) => {
    const target = evt.currentTarget
    const isListboxItem = evt.currentTarget.classList.contains(CLASS_LISTBOX_ITEM)

    if (!isListboxItem) {
      return
    }

    const isAction = target.classList.contains(CLASS_ACTION_ITEM)
    const itemId = target.getAttribute('id') ?? ''

    if (isAction) {
      const action = actions.find(x => x.id === itemId)
      action?.onClick(action.id)
    } else {
      selectOption(itemId)
    }

    dispatchUIAction('option-click')

    /**
     * Reset filtered options after explicit option select.
     * Delay is needed to hide the visible effect from user.
     */
    if ((mode === 'search') && !isAction) {
      delayToNotBlickWhilePopoverIsClosing(() => {
        setItemsToRenderByFilter(null)
      })
    }
  }, [
    actions,
    delayToNotBlickWhilePopoverIsClosing,
    dispatchUIAction,
    mode,
    rawOptions,
    selectOption,
    setItemsToRender
  ])

  const onScrollListbox = useCallback((evt: SyntheticEvent<HTMLDivElement, UIEvent>) => {
    if (!isAsync) {
      return
    }

    const { scrollHeight, scrollTop, clientHeight } = evt.currentTarget

    /*
     * we have dynamic loading component in the bottom
     * If delta scrollTop is too small from the previousScrollTop
     * we do not trigger handler
     *
     * AZ-TODO: check if it still neaded
     */
    if (Math.abs(scrollTop - refPrevScrollTopTrigger.current) <= SCROLL_TOP_TRIGGER_OFFSET) {
      return
    }

    if (scrollHeight - scrollTop <= clientHeight + DEFAULT_BOTTOM_THRESHOLD) {
      refPrevScrollTopTrigger.current = scrollTop
      void getItems(searchValue ?? '')
    }
  }, [isAsync, getItems, searchValue])

  // ========================================== //
  //                   EFFECTS                  //
  // ========================================== //

  /**
   * The props 'options', 'value' and 'inputValue' are closely related
   * and in the same time can be controlled/uncontrolled independently
   * of each other.
   * So we need to cover all possible combinations.
   */

  /**
   * Changes to `props.options` means that options are controllable.
   * In this case we just use `props.options` without fetching any requests.
   * (EffectOptions)
   */
  useEffect(() => {
    if (isAsync) {
      debug(fieldId, 'EffectOptions :: fetchedOptions :: ', fetchedOptions, options)
      const resolvedOptions = fetchedOptions.length === 0
        ? options ?? []
        : fetchedOptions

      setRawOptions(resolvedOptions)
      return
    }

    if (!isAsync && (options === undefined)) {
      return // Uncontrolled, skip
    }

    setRawOptions(options ?? [])
  }, [isAsync, options, fetchedOptions])

  useEffect(() => {
    debug('EffectRawOptions :: rawOptions :: ', rawOptions)
    setItemsToRender(mapOptionsToItems(rawOptions))
  }, [rawOptions, setItemsToRender])

  /**
   * Changes to `props.value` means that selected items are controllable.
   * (EffectValue)
   */
  useEffect(() => {
    if (refIsMultiselect.current) {
      return // TBD
    }

    debug('EffectValue :: defaultValue, value :: ', defaultValue, value)
    const resolvedValue = (value !== undefined
      ? value
      : refIsDefaultValueUsed.current ? undefined : defaultValue) as TOption | null

    refIsDefaultValueUsed.current = true

    if (resolvedValue === undefined) {
      return // Uncontrolled, skip
    }

    /**
     * For the single-select use case - it's safe to erase the whole Map
     * of selected items.
     */
    refSelectedItems.current = new Map()

    if (resolvedValue === null) {
      setSelectedItems([])
      setDisplayValue('')
      return
    }

    const valueItem = mapOptionsToItems([resolvedValue])[0]
    refSelectedItems.current.set(valueItem.id, valueItem)
    setSelectedItems([valueItem])

    if (refInputValue.current === undefined) {
      setDisplayValue(formatDisplayValue(valueItem.value))
    }
  }, [defaultValue, value, formatDisplayValue])

  /**
   * Changes to `props.inputValue` means that Input content is controllable.
   * (EffectInputValue)
   */
  useEffect(() => {
    if (_inputValue === undefined) {
      return // Uncontrolled, skip
    }

    if (refMode.current === 'search') {
      debug('EffectInputValue :: _inputValue :: ', _inputValue)
      setSearchValue(_inputValue ?? '')
      setDisplayValue(_inputValue ?? '')

      if (isAsync) {
        void getItems(_inputValue ?? '', true)
        setItemsToRender([])
      } else {
        setItemsToRenderByFilter(_inputValue ?? '')
      }
    }
  }, [
    _inputValue,
    isAsync,
    getItems,
    setItemsToRenderByFilter
  ])

  // ------------------------------ //

  /**
   * Dropdown UI state machine (SM).
   * Responsible for managing expand/collapse and for tracking focus.
   *
   * This SM's correctness is based on next assumtions:
   *  - popover `onClose` is called only on outside (for the whole dropdown) clicks.
   */
  useEffect(() => {
    const processAction = (): void => {
      const prevUIAction = refPrevUIAction.current
      const lastUIAction = refLastUIAction.current
      const currState = refUIState.current
      const prevState = refPrevUIState.current

      debug(`sm :: received action :: "${lastUIAction ?? '-'}"`)
      debug(`sm :: previous action :: "${prevUIAction ?? '-'}"`)
      debug(`sm :: currState :: "${currState}"`)
      debug(`sm :: prevState :: "${prevState}"`)

      switch (lastUIAction) {
        case 'trigger-click': {
          /**
           * By design, input can't be a container for other element.
           * So in this case there are not bubbling from Trigger element
           * to the "parent" Input element. That's why we need to handle this case in-place.
           * This is different from the SelectDisplay' use case, where SelectDisplay
           * is a proper parent for the Trigger.
           */
          if (mode === 'search') {
            if (['idle', 'focused-collapsed'].includes(currState)) {
              openDropdown()
              focusDisplay()
              gotoState('focused-expanded')
            } else if (currState === 'focused-expanded') {
              closeDropdown()
              focusDisplay()
              gotoState('focused-collapsed')
            }
          }

          break
        }

        case 'select-display-click': {
          if (['idle', 'focused-collapsed'].includes(currState)) {
            openDropdown()
            focusDisplay()
            gotoState('focused-expanded')
          } else if (currState === 'focused-expanded') {
            closeDropdown()
            focusDisplay()
            gotoState('focused-collapsed')
          }

          break
        }

        case 'input-click':
        case 'input-change': {
          if (['idle', 'focused-collapsed'].includes(currState)) {
            openDropdown()
            focusDisplay()
            gotoState('focused-expanded')
          }

          break
        }

        case 'option-click': {
          closeDropdown()
          focusDisplay()
          gotoState('focused-collapsed')
          break
        }

        case 'clear-click': { // does not change state, only focus
          focusDisplay()
          break
        }

        case 'popover-close-requested': {
          closeDropdown()
          processDropdownBlur()

          if (currState === 'focused-expanded') {
            gotoState('idle')
          }

          break
        }

        case 'container-blur': {
          if (isDropdownOpen) {
            debug('skip action because of: "isDropdownOpen" reason')
            break
          }

          if (prevUIAction === 'trigger-click') {
            debug('skip action because of: "prevUIAction === trigger-click" reason')
            break
          }

          processDropdownBlur()
          gotoState('idle')
          break
        }

        default: {
          // DO NOTHING
        }
      }

      refPrevUIAction.current = refLastUIAction.current
      debug('sm :: ===============================')
    }

    debug('sm :: subscribed')
    refEventBus.current.on(ACTION_EVENT, processAction)

    return () => {
      debug('sm :: un-subscribed')
      refEventBus.current.off(ACTION_EVENT, processAction)
    }
  }, [
    closeDropdown,
    isDropdownOpen,
    gotoState,
    focusDisplay,
    openDropdown,
    processDropdownBlur
  ])

  // ========================================== //
  //                  PUBLIC API                //
  // ========================================== //

  return {
    displayValue: _inputValue ?? displayValue,
    isClearVisible: !disableClearable && isClearVisible,
    isDropdownOpen: _isDropdownOpen ?? isDropdownOpen,
    isLoading,
    isOptionSelected,
    itemsToRender,
    onBlurContainer,
    onBlurInput,
    onBlurSelectDisplay,
    onChangeInput,
    onClickInput,
    onClickListboxItem,
    onClickSelectDisplay,
    onClickTrigger,
    onClosePopover,
    onFocusContainer,
    onFocusInput,
    onFocusSelectDisplay,
    onMouseDownClear,
    onScrollListbox,
    selectedItems
  }
}

export default useDropdown
