import { type FC, useMemo } from 'react'
import Dropdown from '@UI/Dropdown'
import _FormFieldHOC from '@UI/FormFieldHOC'

import type { ConditionalValue, DropdownProps as DP } from './types' // AZ-NOTE: do not replace relative import
import type { FormFieldProps, FormCompatible } from '../FormFieldHOC'

export type DV<TOption, TIsMultiple extends boolean> = undefined | ConditionalValue<TOption[], TOption | null, TIsMultiple>
type FCompat<TOption, TIsMultiple extends boolean> = FormCompatible<DP<TOption, TIsMultiple>, DV<TOption, TIsMultiple>>

function FormFieldHOC<TOption, TIsMultiple extends boolean = false> (
  props: FormCompatible<DP<TOption, TIsMultiple>, DV<TOption, TIsMultiple>>
): JSX.Element {
  const Comp = useMemo(() => (
    _FormFieldHOC<FCompat<TOption, TIsMultiple>, DV<TOption, TIsMultiple>>(
      Dropdown as FC<FormFieldProps<DV<TOption, TIsMultiple>>>
    )
  ), [])

  return <Comp {...props} />
}

export default FormFieldHOC
