import { useRef } from 'react'

import { cnx } from '@utils/ui'
import Input from '@UI/Input'
import Popover from '@UI/PopoverV2'

import type { DropdownProps } from './types'
import DefaultListbox from './components/Listbox'
import DefaultTrigger from './components/Trigger'
import DefaultSelectedDisplay from './components/SelectDisplay'
import { POPOVER_CLASSES } from './styles'
import useDropdown from './hook'

function Dropdown <TOption> ({
  className,
  dataTestId,
  endAdornment,
  label,
  mode = 'search',
  renderAction,
  renderListbox: Listbox = DefaultListbox,
  renderNoOptions,
  renderOption,
  renderSelectDisplay: SelectDisplay = DefaultSelectedDisplay,
  placeholder,
  startAdornment,
  style,
  ...hookProps
}: DropdownProps<TOption>): JSX.Element {
  const refRoot = useRef<HTMLDivElement>(null)
  const refInput = useRef<HTMLInputElement>(null)
  const refSelectDisplay = useRef<HTMLDivElement>(null)

  const {
    displayValue,
    isClearVisible,
    isDropdownOpen,
    isLoading,
    isOptionSelected,
    itemsToRender,
    onBlurContainer,
    onBlurInput,
    onBlurSelectDisplay,
    onChangeInput,
    onClickInput,
    onClickListboxItem,
    onClickSelectDisplay,
    onClickTrigger,
    onClosePopover,
    onFocusContainer,
    onFocusInput,
    onFocusSelectDisplay,
    onMouseDownClear,
    onScrollListbox
  } = useDropdown<TOption>({
    ...hookProps,
    mode,
    refRoot,
    refInput,
    refSelectDisplay
  })

  const { actions, disabled, error, id } = hookProps

  // ========================================== //

  const triggerElem = endAdornment ?? (
    <DefaultTrigger
      isClearVisible={isClearVisible}
      isOpen={isDropdownOpen}
      onClickTrigger={onClickTrigger}
      onMouseDownClear={onMouseDownClear}
    />
  )

  return (
    <div
      id={`${id}-container`}
      className={cnx('cf-dropdown-root', className)}
      data-test-id={dataTestId}
      onBlur={onBlurContainer}
      onFocus={onFocusContainer}
      ref={refRoot}
      tabIndex={0}
      style={style}
    >
      {(mode === 'search') && (
        <Input
          autoComplete='off'
          disabled={disabled}
          endAdornment={triggerElem}
          error={error}
          id={`${id}-input`}
          inputRef={refInput}
          label={label}
          onBlur={onBlurInput}
          onChange={onChangeInput}
          onClick={onClickInput}
          onFocus={onFocusInput}
          placeholder={placeholder}
          startAdornment={startAdornment}
          value={displayValue}
        />
      )}

      {(mode === 'select') && (
        <SelectDisplay
          containerRef={refSelectDisplay}
          disabled={disabled}
          endAdornment={triggerElem}
          error={error}
          label={label}
          onBlur={onBlurSelectDisplay}
          onClick={onClickSelectDisplay}
          onFocus={onFocusSelectDisplay}
          selectedItems={[]}
          startAdornment={startAdornment}
          value={displayValue}
        />
      )}

      <Popover
        className={POPOVER_CLASSES}
        isOpen={isDropdownOpen}
        isSameWidth
        onClose={onClosePopover}
        anchorEl={refRoot}
      >
        <Listbox<TOption>
          actions={actions}
          id={`${id}-popup`} // required by useAutocompleteInfiniteList
          isLoading={isLoading}
          isOptionSelected={isOptionSelected}
          onClickItem={onClickListboxItem}
          onScroll={onScrollListbox}
          options={itemsToRender}
          renderAction={renderAction}
          renderNoOptions={renderNoOptions}
          renderOption={renderOption}
        />
      </Popover>
    </div>
  )
}

export default Dropdown
